.conversion {
  min-height: calc(100vh - 7rem);

  &__result-wrapper {
    @extend %card;
    border-radius: rem-calc(8);
    background-color: color(white);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin: 5rem 0;
  }

  &__result {
    &-amount {
      color: color(gray-5);
    }

    &-currency {
      color: color(gray-8);
    }

  }

  &__equality {
    position: relative;
    font-weight: 900;
    padding: 2rem 5rem;

    &::before {
      position: absolute;
      content: '';
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 1px;
      background-color: color(gray-c);
    }

    p {
      color: color(gray-c);
      padding: rem-calc(8);
      background-color: color(white);
      position: absolute;
      transform: translate(-50%, -50%);
    }
  }
}

.conversion-form {
  .conversion-submit-btn {
    background-color: color(primary);

    &:hover {
      background-color: darken(color(primary), 5%);
    }
  }

  // Amount Input
  .ant-input-number {
    width: 100%;
  }
}
