@import url(https://fonts.googleapis.com/css?family=Work+Sans:600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Work Sans', Arial, Helvetica, sans-serif; }

p {
  margin: 0rem; }

.display {
  font-weight: 700;
  font-size: 2rem; }
  @media screen and (min-width: 1024px) {
    .display {
      font-size: 2.25rem; } }

.title {
  font-size: 1.75rem;
  font-weight: 700; }
  @media screen and (min-width: 1024px) {
    .title {
      font-size: 2rem; } }

.text {
  font-size: 1rem;
  font-weight: 700; }
  @media screen and (min-width: 1024px) {
    .text {
      font-size: 1.125rem; } }

.subtitle {
  font-size: 1.25rem;
  font-weight: 500; }
  @media screen and (min-width: 1024px) {
    .subtitle {
      font-size: 1.5rem; } }

.caption {
  font-size: 0.75rem;
  font-weight: 400; }
  @media screen and (min-width: 1024px) {
    .caption {
      font-size: 0.625rem; } }

.header {
  position: fixed;
  width: 100%;
  align-items: center;
  box-shadow: 0 0.0625rem 0.5rem rgba(0, 0, 0, 0.1);
  display: flex;
  max-height: 3.5rem;
  padding: 1rem 0.75rem;
  background-color: #ffffff;
  z-index: 1;
  color: #ffffff; }
  .header h1 {
    font-size: 1.25rem;
    font-weight: 700;
    text-transform: uppercase;
    margin: 0; }

.nav__item, .nav__item--active {
  background-color: #ffffff;
  color: #aaaaaa;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem 0.75rem;
  text-align: center;
  width: 25%;
  cursor: pointer;
  -webkit-transform-origin: center;
          transform-origin: center; }
  @media screen and (min-width: 568px) {
    .nav__item, .nav__item--active {
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content; } }
  .nav__item p, .nav__item--active p {
    font-size: 0.6rem;
    margin: 0;
    margin-top: 0.125rem;
    text-transform: uppercase; }
  .nav__item--active {
    color: #3D6DD1; }

.ant-tabs-nav-container {
  width: 100%; }

.ant-tabs-nav {
  width: 100%; }
  .ant-tabs-nav > div:first-child {
    display: flex;
    background-color: #77A7DF;
    color: #ffffff; }
    .ant-tabs-nav > div:first-child .ant-tabs-tab {
      width: 100%;
      margin: 0;
      text-align: center;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
      .ant-tabs-nav > div:first-child .ant-tabs-tab:hover {
        color: #eeeeee;
        background-color: #629ada; }
    .ant-tabs-nav > div:first-child .ant-tabs-tab-active {
      color: #ffffff;
      background-color: #3D6DD1; }
      .ant-tabs-nav > div:first-child .ant-tabs-tab-active:hover {
        background-color: #2f60c6; }

.conversion__result-wrapper {
  box-shadow: 0 0.5rem 1.25rem rgba(0, 0, 0, 0.1);
  padding: 1rem; }

.currency-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  margin-bottom: 0.5rem;
  background-color: #ffffff;
  border-radius: 0.25rem;
  box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.1);
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s; }
  .currency-card:hover {
    box-shadow: 0 0.0625rem 0.5rem rgba(0, 0, 0, 0.1); }
  .currency-card__wrapper {
    display: flex;
    justify-content: space-between;
    flex-basis: 100%;
    margin-right: 2rem; }
  .currency-card__content {
    display: flex;
    align-items: center; }
  .currency-card__image-wrapper {
    margin-right: 1rem;
    position: relative; }
    .currency-card__image-wrapper img {
      max-height: 2rem; }
    .currency-card__image-wrapper:after {
      position: absolute;
      content: "";
      left: 0;
      padding-right: 3rem; }
  .currency-card__caption {
    color: #aaaaaa;
    text-transform: uppercase;
    margin-bottom: 0; }
  .currency-card__title, .currency-card__price {
    margin-bottom: 0;
    font-weight: bold;
    color: #000000; }
  .currency-card__title {
    letter-spacing: 0.01562rem; }
  .currency-card__numbers {
    text-align: right;
    display: flex;
    flex-direction: column; }

.footer {
  position: fixed;
  bottom: 0;
  box-shadow: 0 -0.0625rem 0.5rem rgba(0, 0, 0, 0.1);
  display: flex;
  max-height: 3.5rem;
  width: 100%; }
  @media screen and (min-width: 568px) {
    .footer {
      margin: 3.5rem 0;
      padding: 2rem 0 100vh 0;
      background-color: #ffffff;
      top: 0;
      left: 0;
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
      min-width: 5rem;
      flex-direction: column;
      align-items: center;
      z-index: 0; } }

.conversion {
  min-height: calc(100vh - 7rem); }
  .conversion__result-wrapper {
    border-radius: 0.5rem;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin: 5rem 0; }
  .conversion__result-amount {
    color: #555555; }
  .conversion__result-currency {
    color: #888888; }
  .conversion__equality {
    position: relative;
    font-weight: 900;
    padding: 2rem 5rem; }
    .conversion__equality::before {
      position: absolute;
      content: '';
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #cccccc; }
    .conversion__equality p {
      color: #cccccc;
      padding: 0.5rem;
      background-color: #ffffff;
      position: absolute;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%); }

.conversion-form .conversion-submit-btn {
  background-color: #3D6DD1; }
  .conversion-form .conversion-submit-btn:hover {
    background-color: #2f60c6; }

.conversion-form .ant-input-number {
  width: 100%; }

body {
  box-sizing: border-box; }

.main-wrapper {
  margin-top: 3.5rem;
  position: fixed;
  height: calc(100vh - 7rem);
  width: 100%;
  overflow-y: scroll;
  background-color: #fcfcfc; }
  @media screen and (min-width: 568px) {
    .main-wrapper {
      height: calc(100vh - 3.3rem);
      padding-left: 5rem; } }

.conversion-submit-btn {
  border: 0;
  background-color: #3D6DD1; }

